<template>
  <div>
    <custom-navbar></custom-navbar>
    <b-form @submit="onSubmit" @reset="onReset" v-if="show"  class="newIssues">
      <b-card title="新增專案">
        <hr>
        <b-container fluid>
          <b-row v-for="inputInfo in inputInfos" :key="inputInfo.label">
            <b-col sm="2"><label :for="`type-${type}`">{{inputInfo.label}}:</label></b-col>
            <b-col sm="10"><b-form-input :id="`${inputInfo.label}`" :type="inputInfo.type" :placeholder="inputInfo.placeholder"></b-form-input></b-col>
          </b-row>
        </b-container>
        <br>
        <b-button type="submit" variant="primary">送出</b-button>
        <b-button type="reset" variant="danger">清空</b-button>
      </b-card>
    </b-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inputInfos: [
        {label: '專案名稱', type: 'text'},
        {label: '專案代號', type: 'text'},
        {label: '專案子項目編號', type: 'text'},
        {label: '專案子項目名稱', type: 'text'},
        {label: '任務項目編號', type: 'text'},
        {label: '任務內容', type: 'text'},
        {label: '工作項目', type: 'text'},
        {label: '起始時間', type: 'date'},
        {label: '預計完成時間', type: 'date'}
      ],
      form: {
        email: '',
        name: '',
        food: null,
        checked: []
      },
      foods: [
        { text: 'Select One', value: null },
        'Carrots', 'Beans', 'Tomatoes', 'Corn'
      ],
      show: true,
      file: null
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.form))
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      this.form.email = ''
      this.form.text = ''
      this.form.food = null
      this.form.checked = []
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .newIssues {
    margin-top: 2%;
    margin-right: 5%;
    margin-left: 5%;
  }
  label {
    margin-top: 10px;
  }
</style>



