<template>
  <div>
    <custom-navbar></custom-navbar>
    <div class="issueList">
      <b-card title="工作清單">
        <b-table striped hover responsive :items="items" :fields="fields">
          <template slot="專案計畫" slot-scope="row">{{row.value}}</template>
          <template slot="專案名稱" slot-scope="row">{{row.value}}</template>
          <template slot="專案子項目編號" slot-scope="row">{{row.value}}</template>
          <template slot="專案子項目名稱" slot-scope="row">{{row.value}}</template>
          <template slot="任務項目編號" slot-scope="row">{{row.value}}</template>
          <template slot="任務內容" slot-scope="row">{{row.value}}</template>
          <template slot="工作項目" slot-scope="row">{{row.value}}</template>
          <template slot="起始時間" slot-scope="row">{{row.value}}</template>
          <template slot="預計完成時間" slot-scope="row">{{row.value}}</template>
          <template slot="詳細內容" slot-scope="row">
            <b-btn to="./projectDetail">詳細內容</b-btn>
          </template>
        </b-table>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: [
        {
          專案計畫: '智慧營運計畫',
          專案名稱: '3C使用者分群企業智能化研發計畫',
          專案子項目編號: 'A',
          專案子項目名稱: '智能服務平台建置',
          任務項目編號: 'A2',
          任務內容: '智能服務資訊系統開發、資料蒐集分析及返饋應用',
          工作項目: '智能服務',
          起始時間: '106/12/31',
          預計完成時間: '106/12/31'
        },
        {
          專案計畫: '智慧營運計畫',
          專案名稱: '3C使用者分群企業智能化研發計畫',
          專案子項目編號: 'C',
          專案子項目名稱: '智能供應鏈平台建置',
          任務項目編號: 'C1',
          任務內容: '智能供應鏈系統連線及開發',
          工作項目: '智能供應鏈',
          起始時間: '106/12/31',
          預計完成時間: '106/12/31'
        }
      ],
      fields: [
        {key: '專案計畫', label: '專案計畫'},
        {key: '專案名稱', label: '專案名稱'},
        {key: '專案子項目編號', label: '專案子項目編號'},
        {key: '專案子項目名稱', label: '專案子項目名稱'},
        {key: '任務項目編號', label: '任務項目編號'},
        {key: '任務內容', label: '任務內容'},
        {key: '工作項目', label: '工作項目'},
        {key: '起始時間', label: '起始時間'},
        {key: '預計完成時間', label: '預計完成時間'},
        {key: '詳細內容', label: '詳細內容'}
      ],
      modalInfo: { title: '', content: '' }
    }
  },
  methods: {
    info (item, index) {
      alert(item + index)
    },
    resetModal () {
      this.modalInfo.title = ''
      this.modalInfo.content = ''
    },
    showAler () {
      alert('test')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .issueList {
      margin-top: 2%;
      margin-right: 5%;
      margin-left: 5%;
    }
</style>



