        {key: '核准者', label: '核准者'},
        {key: '動作', label: '動作'}
      ],
      modalInfo: { title: '', content: '' }
    }
  },
  methods: {
    info (item, index) {
      alert(item + index)
    },
    resetModal () {
      this.modalInfo.title = ''
      this.modalInfo.content = ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .projectDetail {
      margin-top: 2%;
      margin-right: 5%;
      margin-left: 5%;
    }
</style>



